import React,{useState} from 'react';
import Card from './Card';

import './App.css';

function App() {
  const [form,setform]=useState(false);
  const [name,setname]=useState();
  const[des,setdes]=useState();
  const[size,setsize]=useState();
  const [id,setid]=useState(0);
  const [alldata ,setdata]=useState([{
    Name:'',
    Des:'',
    Size:'',
    id:'a'
}]);

  const updatedata=()=>
  {
    setdata((oldata)=>{
      return([...oldata,{Name:name,Des:des,Size:size}])
    });
    setid(id+1);
  }


  const deletedata=(id_no)=>{
    setdata((olditem)=>{
			return olditem.filter((arrelem,index)=>{
				return index!==id_no;
			})
		})
  }
  

  return (
    <div className="App">
      <h1>ADD A NEW PROPERTY</h1>
      <br/>
      <button onClick={()=>setform(!form)}>Add Property </button>
      {form===true?  <div>
            <br></br>
               NAME : <input type="text" placeholder="Name" value={name} onChange={(e)=>setname(e.target.value)}/>
                <br/><br></br>
        DESC. : <input type="text" placeholder="Description" value={des} onChange={(e)=>setdes(e.target.value)}/>
                <br/><br></br>
               SIZE :  <input type="number" placeholder="size" value={size} onChange={(e)=>setsize(e.target.value)}/>
                <br/><br/>
                <button  onClick={updatedata}><i className="fas fa-plus-circle" style={{color:'green'}}></i></button>



            
        </div>:null}


      
      <br/>
      <h2>List of Properties</h2>
      <div >
      {alldata.map((cvalue,index)=>{
        if(cvalue.id=='a'){}
        else{

          return(<Card name={cvalue.Name} id={index} funct={deletedata} des={cvalue.Des} size={cvalue.Size}/>)
        }
      })}
     

      </div>
      
      
    </div>
  );
}

export default App;
