import React from 'react'

const Card = (props) => {
    const dell=()=>{
        props.funct(props.id);
    }
    return (
        <div className="card" >
        <button id="dell" onClick={dell}><i className="fas fa-trash-alt" style={{color:'crimson'}}></i></button>
        Name : <span >{props.name}</span><br></br>
        Description : <span >{props.des}</span><br></br>
        Size : <span >{props.size}</span><br></br>
        {props.id}
       


      </div>
    )
}

export default Card
